@use 'variables' as *;


@mixin telefono {
    @media screen and (min-width:$cellphone) {
        @content;
    }
}
@mixin tabletMax {
    @media screen and(max-width:$tabletMax){
        @content;
    }
}
@mixin tablet {
    @media screen and (min-width:$tablet){
        @content;
    }
}
@mixin desktop {
    @media screen and (min-width:$desktop){
        @content;
    }
}

@mixin grid($columna, $gap){
    display: grid;
    grid-template-columns: repeat($columna,1fr);
    gap:$gap;
}